<template>
  <v-app id="inspire">
    <v-app-bar app color="green lighten-5">
      <div class="d-flex align-center">
        <v-img
          alt="snoopy Logo"
          class="shrink mr-2"
          contain
          src="../assets/snoop.png"
          transition="scale-transition"
          width="50"
        />

        <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
          min-width="150"
          src="../assets/barkingLogo.png"
          width="100"
        />
      </div>

      <v-spacer></v-spacer>

      <!--2. 로그인 되었을때-->
      <template v-if="isUserLogin">
        <v-btn text @click="logoutUser">
          LOGOUT
          <v-icon class="ml-2">mdi-open-in-new</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text href="/login">
          <span class="mr-2">LOGIN</span>
          <v-icon>mdi-open-in-new</v-icon>
        </v-btn>
      </template>
    </v-app-bar>
    <v-navigation-drawer v-if="isUserLogin" v-model="drawer" app>
      <v-sheet color="green lighten-5" class="pa-4">
        <v-avatar class="mb-4" color="grey lighten-1" size="64"></v-avatar>

        <div>{{$store.state.username}}</div>
      </v-sheet>

      <v-divider></v-divider>

      <v-list>
        <v-list-item v-for="[icon, text, tolink] in links" :key="icon" link :to="tolink">
          <v-list-item-icon>
            <v-icon>{{ icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-main class="ma-auto">
      <transition name="fage">
        <router-view></router-view>
      </transition>
    </v-main>
  </v-app>
</template>

<script>
export default {
  computed: {
    isUserLogin() {
      return this.$store.getters.isLogin;
    }
  },
  methods: {
    logoutUser() {
      this.$store.commit("CLEAR_USERNAME");
      this.$router.push("/login");
    }
  },
  data: () => ({
    drawer: null,
    links: [
      ["mdi-inbox-arrow-down", "메인", "/main"],
      ["mdi-send", "렐레", "/userlist"],
      ["mdi-delete", "블레", "/dev_2"],
      ["mdi-alert-octagon", "랄라", "/dev"]
    ]
  })
};
</script>
<style>
.fage-enter-active,
.fage-leave-active {
  transition: opacity 0.5s;
}
.fage-enter, .fage-leave-to /* .fage-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>