import Vue from 'vue'
import VueRouter from 'vue-router'
import AppLogin from '../components/AppLogin'

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        { path: '/login', component: AppLogin },
        { path: '/main', component: () => import('../components/AppMain') },
        { path: '/userlist', component: () => import('../components/AppUserList') },
        { path: '/dev', component: () => import('../components/IncommingYear') },
        { path: '/dev_2', component: () => import('../components/TableTest0516') },
        { path: '/dev_1', component: () => import('../components/TestComponent') },
        { path: '/dev_3', component: () => import('../components/TableTest0516') },
        { path: '*', redirect: '/main' },
    ],
    // mode: 'history',
})

export default router