<template>
  <v-container class="py-8 px-6" fluid>
    <v-card class="overflow-hidden" width="500px" color="green lighten-5">
      <v-toolbar flat color="green lighten-5">
        <v-icon>mdi-account</v-icon>
        <v-toolbar-title class="font-weight-light">LOGIN</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-card-text>
        <v-text-field v-model="id" color="grey" label="ID" class="mx-4"></v-text-field>
        <v-text-field v-model="pw" type="password" color="grey" label="PW" class="mx-4"></v-text-field>
        <v-checkbox v-model="checkbox" :label="`: 아이디 ${checkbox? '저장':'저장안함'}`" class="mx-4"></v-checkbox>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="accent" @click="login">LOGIN</v-btn>
        <v-btn color="secondary" @click="initForm">CANCEL</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
// import { loginUser } from "../api";
// import { saveUserToCookie, saveAuthToCookie } from "@/cookies";
export default {
  data() {
    return {
      id: "",
      pw: "",
      checkbox: true,
      hasSaved: false,
    };
  },

  methods: {
    async login() {
      try {
        //비즈니스로직
        const userData = {
          username: this.id,
          password: this.pw,
        };
        // const { data } = await loginUser(userData);
        // console.log(data.token);
        // this.$store.commit("setToken", data.token);
        // saveAuthToCookie(data.token);
        // this.$store.commit("setUsername", data.user.username);
        // saveUserToCookie(data.user.username);
        // this.$router.push("/main");
        await this.$store.dispatch("LOGIN", userData);
        this.$router.push("/main");
      } catch (error) {
        //에러핸들링
        console.log(error.reponse.data);
        this.logMessage = error.reponse.data;
      } finally {
        this.initForm();
      }
    },
    initForm() {
      this.id = "";
      this.pw = "";
    },
  },
};
</script>

